import { useState } from "react"
import BNBLOGO from "../../../assets/img/svg/bnb-logo.svg"
import VLKLOGO from "../../../assets/img/svg/vlk.svg"
import PAYMENT from "../../../assets/img/svg/secure-payment.svg"
import NumberFormat from 'react-number-format'
import '../PricingItem/Pricing.css'
import axios from 'axios';
import { StatusCodes } from 'http-status-codes'

import { CModal, CModalTitle, CModalHeader, CModalBody, CModalFooter, CButton, CAlert } from '@coreui/react'

const PricingItem = ({ type, Round, dollar, bonus, maxtoken, minmax }) => {
  const [visible, setVisible] = useState(false)
  const [alertVisible, setAlertVisible] = useState(false)
  const [vlk, setVlk] = useState()
  const [bnb, setBnb] = useState()
  const [bnbAmount, setBnbAmount] = useState(1)
  const [contract, setContract] = useState("")

  const rate = 200000000

  const changeBnb = (e) => {
    setBnbAmount(e.floatValue)
    setVlk(e.floatValue * rate)
  }

  const changeVlk = (e) => {
    setBnbAmount(e.floatValue/rate)
    setBnb(e.floatValue / rate)
  }

  function ClipboardCopy({ copyText }) {
    const [isCopied, setIsCopied] = useState(false);

    // This is the function we wrote earlier
    async function copyTextToClipboard(text) {
      if ('clipboard' in navigator) {
        return await navigator.clipboard.writeText(text);
      } else {
        return document.execCommand('copy', true, text);
      }
    }

    // onClick handler function for the copy button
    const handleCopyClick = () => {
      // Asynchronously call copyTextToClipboard
      copyTextToClipboard(copyText)
        .then(() => {
          // If successful, update the isCopied state value
          setIsCopied(true);
          setTimeout(() => {
            setIsCopied(false);
          }, 1500);
        })
        .catch((err) => {
          console.log(err);
        });
    }

    return (
      <div>
        <input className="address" type="text" value={copyText} readOnly />
        {/* Bind our handler function to the onClick button property */}
        <button className="address-button " onClick={handleCopyClick}>
          <span>{isCopied ? 'Copied!' : 'Copy'}</span>
        </button>
      </div>
    );
  }

  const sendMail = () => {
    setAlertVisible(true)
    const data = {
      "personalizations":[
        {"to":[{"email":"vikingdev666@gmail.com","name":"ACQUISTO"}],"subject":"NUOVO ACQUISTO PRIVATE SALE"}],
        "content": [{"type": "text/plain", "value": "QUESTO CONTRATTO HA ACQUISTATO " + bnbAmount + "BNB - CONTRATTO " + contract}],
        "from":{"email":"vikingdev666@gmail.com","name":"VIKINGDEV"},
        "reply_to":{"email":"vikingdev666@gmail.com","name":"VIKINGDEV"}}

    axios({
      method: "post",
      url: "https://api.sendgrid.com/v3/mail/send",
      headers: {
        "Access-Control-Allow-Origin" : "*",
        "Content-type": "Application/json",
        "Authorization": `Bearer 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`
      },
      data: data
    }).then((res) => {
      console.log( res )
      if (res && res.status === StatusCodes.CREATED) {
        console.log( res )
      }
    }).catch((err) => {
      console.log(err)
    }) 
    setVisible(false)
  }

  return (
    <>    
      <CAlert color="primary" dismissible visible={alertVisible} onClose={() => setAlertVisible(false)}>Order sent, thanks for your support!</CAlert>
      <div className="col-lg-9 col-sm-9 col-xs-12">
        <div className="pricing-item">
          <div className="exrate-container">
            <div className="exrate-title">Exchange Rate: </div>
            <div className="label exrate-spacer">1 BNB <img src={BNBLOGO} style={{width: '30px'}}/> = {rate} VLK <img src={VLKLOGO} style={{width: '30px', backgroundColor:"white", borderRadius:'50%', padding:2}}/></div>
          </div>
          <h4>Amount of contribute</h4> 
          <div className="label spacer">Enter the number of Valknut tokens you want to buy to get the price in BNB, or enter the number of BNB you want to spend to calculate how many Valknut tokens you will receive.</div>

          <div className="exrate-container">
            <div className="inner-box-container">
              <NumberFormat
                thousandsGroupStyle="thousand"
                defaultValue={1}
                type="text"
                allowNegative={false}
                thousandSeparator={true}
                decimalSeparator="."
                displayType="input"
                isNumericString={true}
                onValueChange={(values) => changeBnb(values)}/><div className="inner-label">BNB</div>
            </div>
            <div className="inner-label"> = </div>
            <div className="inner-box-container">
              <div className="inner-value">{vlk}</div>
              <div className="inner-label">VLK</div>
            </div>
          </div>
          
          <div className="exrate-container">
            <div className="inner-box-container">
              <NumberFormat
                thousandsGroupStyle="thousand"
                defaultValue={rate}
                type="text"
                allowNegative={false}
                thousandSeparator={true}
                decimalSeparator="."
                displayType="input"
                isNumericString={true}
                onValueChange={(values) => changeVlk(values)}/><div className="inner-label">VLK</div>
            </div>
            <div className="inner-label"> = </div>
            <div className="inner-box-container">
              <div className="inner-value">{bnb}</div>
              <div className="inner-label">BNB</div>
            </div>
          </div> 
          <div className="payment-container"><div className="payment-button" onClick={()=>setVisible(!visible)}><div style={{color:'white'}}>MAKE PAYMENT<img src={PAYMENT} className="payment-img"/></div></div></div>
        </div>

        <CModal alignment="center" visible={visible} onClose={() => setVisible(false)} backdrop={"static"}>
          <CModalHeader closeButton={false} className="modal-content-style">
            <CModalTitle>Confirm Your Payment</CModalTitle>
          </CModalHeader>
          <CModalBody className="modal-content-style">
            <div className="modal-label">Please send the amount of <div style={{color:"cornflowerblue", marginLeft:'10px'}}><strong>{bnbAmount} BNB</strong></div></div>
            <div className="modal-label">to the following wallet address:</div>
            <ClipboardCopy copyText="0x5C62dED198F6154D08a4fD0a979a0CBb8874528A"/>
            <div className="modal-label-small">To speed up the verification process, enter the address of the wallet you want to use for trading</div>
            <input type="text" style={{width: '100%'}} onChange={(e)=>setContract(e.target.value)}/>


          </CModalBody>
          <CModalFooter className="modal-content-style">
            <CButton color="primary" onClick={() => sendMail()}>Confirm payment</CButton>
            <CButton color="secondary" onClick={() => setVisible(false)}>
              Cancel order
            </CButton>
            
          </CModalFooter>
        </CModal>
      </div>
    </>
  );
}

